* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.app-wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
  display: grid;

  grid-template-areas: "h h" "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /*grid-gap: 10px;*/
}

.nav {
  grid-area: n;
  background-color: blanchedalmond;
  padding: 20px;
}

.nav a {
  text-decoration: none;
}

.a--def {
  color: white;
}

.a--act {
  color: gold;
}

.app-wrapper-content {
  grid-area: c;
}


